import { Injectable } from '@angular/core';

@Injectable({
    providedIn: 'root'
})
export class CommonUtilService {

    constructor() { }

    public testService(name: String) {
        console.log('My Name is ' + name);
    }


    public checkRegex(regex: RegExp, data: any) {
        return regex.test(data);
    }
}
